.widget-item-wrapper {
  margin: 5px 20px;
  background: white;
}

.widget-item-wrapper.react-draggable-dragging {
  box-shadow: 1px 1px 6px 3px #ff9800;
  z-index: 1;
}

.widget-item-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: relative;
  width: 345px;
  min-height: 160px;
  padding: 20px;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  text-align: center;
  user-select: none;
}

.widget-item-wrapper .widget-item-container {
  background-color: rgb(47, 0, 47);
}

.widget-item-image {
  height: 140px;
}

.widget-item-bottom-block {
  margin-top: 10px;
  text-align: center;
}

.widget-item-container .widget-item-controls-btn {
  position: absolute;
  top: 0;
  display: none;
}

.sortable .widget-item-container {
  cursor: grab;
  user-select: none;
}

.carousel-container:not(.sortable) .widget-item-container:hover .widget-item-controls-btn {
  display: block;
}

.widget-item-edit-btn {
  right: 0;
}

.widget-item-copy-btn {
  left: 0;
}

.modal-handler {
  overflow-y: auto;
}

.modal-container {
  width: 600px;
  margin: 0 auto;
  padding: 5px 20px 20px 20px;
  overflow-y: auto;
  border: 2px solid #000;
  background-color: #eeefed;
}

.modal-container .block-title {
  margin-top: 20px;
}

.modal-container .copy-env-dropdown {
  width: 100%;
  margin-top: 20px;
  margin-bottom: 20px;
}

.modal-container .copy-btns-container {
  width: 100%;
}

.modal-container .input-file {
  font-size: 1rem;
}

.modal-container .save-btn {
  margin-left: 10px;
}
